<template>
  <div class="wrap">
    <h1>5월 마지막주 영화 예매 순위</h1>
    <ul class="movies">
      <li v-for="movie in movies" class="item" :key="movie.id">
        <span class="rank">{{ movie.id }}</span>
        <router-link :to="{ name: 'show', params: { id: movie.id } }">
          <img v-bind:src="movie.poster" class="poster" />
        </router-link>
        <div class="detail">
          <strong class="tit">{{ movie.title }}</strong>
          <span class="rate">
            예매율
            <span class="num">{{ movie.rate }}</span>
          </span>
          <router-link
            :to="{ name: 'show', params: { id: movie.id } }"
            class="link"
            >자세히보기</router-link
          >
        </div>
      </li>
    </ul>
    <h1>사용자 정보를 입력하세요.</h1>
    <form name="user" method="post" action="/regist">
      <div class="input-row">
        <label for="userid">아이디</label>
        <input id="userid" v-model="userid" name="userid" type="text" />
      </div>
      <div class="input-row">
        <label for="name">이름</label>
        <input id="name" v-model="name" name="name" type="text" />
      </div>
      <div class="input-row">
        <label for="address">주소</label>
        <input id="address" v-model="address" name="address" type="text" />
      </div>
      <button type="submit">전송</button>
    </form>
  </div>
</template>
<script>
export default {
  created() {
    // 컴포넌트가 생성될 때, /api/movies에 요청을 보냅니다.
    this.$http.get("/api/movies").then(response => {
      this.movies = response.data;
    });
  },
  data() {
    return {
      movies: [],
      userid: "",
      name: "",
      address: ""
    };
  }
};
</script>
